@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC|Red+Hat+Text&display=swap);
a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
}

body {
  margin: 0;
  font-family: 'Red Hat Text', 'Noto Sans TC', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1f1f1f;
  padding-top: 90px;
}

main {
  min-height: 90vh;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255,255,255,0.8);
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
  z-index: 99;
}

.h-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .nav {
  margin: 0 -20px;
}

.header .nav li {
  position: relative;
  padding: 0 20px;
}

.header .nav li a {
  display: flex;
  height: 90px;
  align-items: center;
  justify-content: center;
}

.footer {
  padding: 90px 0;
  background-color: #f3f3f3f3;
  border-bottom: 30px solid #fff;
  position: relative;
}

.totop {
  outline: none !important;
  background: rgba(255,255,255,0.5);
  width: 40px;
  height: 40px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  font-size: 0;
  border: 0;
  border-radius: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' xmlns='http://www.w3.org/2000/svg' fill-rule='evenodd' clip-rule='evenodd'%3E%3Cpath d='M11 2.206l-6.235 7.528-.765-.645 7.521-9 7.479 9-.764.646-6.236-7.53v21.884h-1v-21.883z'/%3E%3C/svg%3E");
  background-size: 24px auto;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  visibility: hidden;
  opacity: 0;
}

.wrapper.scroll-down .totop {
  visibility: visible;
  opacity: 1;
}

.vertical-timeline::before {
  background-color: #f9f9f9;
}

.vertical-timeline--two-columns .vertical-timeline-element-content::before {
  border-left-color: #f2f2f2;
}

.vertical-timeline-element-content {
  background-color: #f2f2f2;
}

.vertical-timeline--animate .vertical-timeline-element-icon.bounce-in {
  background: #ddd !important;
}

.submenu {
  position: absolute;
  visibility: hidden;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 0;
  background-color: #fff;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  padding: 5px 0;
  top: 100%;
  width: 200px;
  max-width: 300px;
  left: 0;
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
}

li:hover > .submenu {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.submenu .nav {
  display: block;
  margin: 0;
}

.submenu .nav li {
  padding: 0;
}

.submenu .nav li a {
  display: block;
  height: auto;
  padding: 5px 20px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.submenu .nav li a:hover {
  background-color: #f9f9f9;
}

.menu-tg {
  display: none;
}

.wrapper {
  overflow: hidden;
}

.h-logo h1 {
  margin: 0;
}

.submenu-tg {
  display: none;
}

@media only screen and (min-width: 1170px) {
  .vertical-timeline--two-columns .vertical-timeline-element-icon {
    width: 30px;
    height: 30px;
    margin-top: 15px;
    margin-left: -15px;
  }
  .vertical-timeline--two-columns .vertical-timeline-element:nth-child(even):not(.vertical-timeline-element--left) .vertical-timeline-element-content::before, .vertical-timeline--two-columns .vertical-timeline-element.vertical-timeline-element--right .vertical-timeline-element-content::before {
    border-right-color: #f2f2f2;
  }
}

@media only screen and (max-width: 1199px) {
  .menu {
    position: fixed;
    visibility: hidden;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .menu-overlap {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .wrapper.menu-open .menu {
    visibility: visible;
  }
  .menu > .nav {
    background: #fff;
    max-width: 80vw;
    width: 300px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    box-shadow: -1px 0 10px rgba(0,0,0,0.1);
    display: block;
    padding: 15px 0;
    margin: 0;
  }
  .wrapper.menu-open .menu > .nav {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  .header .nav li {
    padding: 0;
  }
  .header .nav li a {
    display: block;
    height: auto;
    padding: 10px 20px;
  }
  .menu-tg {
    display: block;
    width: 80px;
    height: 80px;
    border: 0;
    border-radius: 0;
    outline: none !important;
    background: none;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' %3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 1.1//EN' 'http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd'%3E%3Csvg enable-background='new 0 0 512 512' id='Layer_1' version='1.1' viewBox='0 0 512 512' xml:space='preserve' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg%3E%3Cpath d='M501.5,79.7H10.5C4.7,79.7,0,75,0,69.2c0-5.8,4.7-10.5,10.5-10.5h490.9c5.8,0,10.5,4.7,10.5,10.5 C512,75,507.3,79.7,501.5,79.7z' fill='%236A6E7C'/%3E%3Cpath d='M501.5,266.5H10.5C4.7,266.5,0,261.8,0,256c0-5.8,4.7-10.5,10.5-10.5h490.9c5.8,0,10.5,4.7,10.5,10.5 C512,261.8,507.3,266.5,501.5,266.5z' fill='%236A6E7C'/%3E%3Cpath d='M501.5,453.4H10.5c-5.8,0-10.5-4.7-10.5-10.5s4.7-10.5,10.5-10.5h490.9c5.8,0,10.5,4.7,10.5,10.5 S507.3,453.4,501.5,453.4z' fill='%236A6E7C'/%3E%3C/g%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 24px auto;
    padding: 0;
  }
  .h-logo h1 {
    font-size: 2rem;
  }
  .submenu-tg {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 44px;
    height: 44px;
    background: none;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' %3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 1.1//EN' 'http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd'%3E%3Csvg enable-background='new 0 0 100 100' id='Layer_1' version='1.1' viewBox='0 0 100 100' xml:space='preserve' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cpolygon fill='%23010101' points='23.1,34.1 51.5,61.7 80,34.1 81.5,35 51.5,64.1 21.5,35 23.1,34.1 '/%3E%3C/svg%3E");
    background-size: 30px auto;
    border: 0;
    border-radius: 0;
    outline: none !important;
    font-size: 0;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }
  .submenu-tg.open {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  .submenu {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
    width: 100%;
    max-width: none;
    visibility: visible !important;
    -webkit-transform: none !important;
            transform: none !important;
    display: none;
  }
  .submenu-tg.open + .submenu {
    display: block;
  }
  .vertical-timeline-element-content::before {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    border-right: 0;
  }
}

